/*
 * ==================== COLORS ====================
*/

:root {
	--lightgray: #f1f1f1;
	--darkgray: #3b3b3b;
	--lavender: #9983ee;
	--purple: #5734e3;
	--jade: #479289;
	--emerald: #0d6166;
}

/*
 * ==================== GLOBAL ====================
*/

html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
	margin: 0;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, CircularStd, BlinkMacSystemFont, 'Segoe UI',
		'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
		'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
